var fixHomeMidBanner = function fixHomeMidBanner() {
  var mainBanner = $('#cabecalho + .secao-banners .banner.cheio');
  var midBanner = $('#cabecalho + .secao-banners .banner.cheio + .banner');
  $('.vitrine-mas-vendido').before(midBanner);
};

var removeCpf = function removeCpf() {
  $('#rodape > div .conteiner p[style="margin-bottom: 0;"]').html($('#rodape > div .conteiner p[style="margin-bottom: 0;"]').html().replace(' - CPF: 215.068.468-31', ''));
};

var createFooter = function createFooter() {
  var logoWhite = '<img class="logo_footer" src="https://loja-toni-armas.s3.amazonaws.com/img/logo_white.png" />';
  var contBlocks = $('#rodape .institucional .conteiner > .row-fluid > div > .row-fluid');
  var payments = $('#rodape .pagamento');
  var seals = $('#rodape .selos');
  var newsletter = $('.componente.newsletter');
  contBlocks.prepend(logoWhite).append(payments).append(seals).append(newsletter);
};

